/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

.l-footer {
  background-color: #FFD664;
  height: 180px;
  width: 1080px;
  margin: 0 auto;
  padding: 30px;
  box-sizing: border-box;
  line-height: 19px;
  font-size: 16px;
  letter-spacing: 0.5;
}

.l-footer .footer {
  display: flex;
}

.l-footer .footer .footer__info {
  margin-right: 56px;
}

.l-footer .footer .footer__info img {
  margin-bottom: 8px;
}

.l-footer .footer .footer__info p {
  margin-bottom: 8px;
}

.l-footer .footer .footer__menu .menu {
  display: flex;
}

.l-footer .footer .footer__menu .menu li a {
  color: #000;
  text-decoration: none;
}

.l-header {
  display: none;
}

.l-inner {
  margin: 0 auto;
  max-width: 1080px;
  width: 100%;
  height: inherit;
  padding-right: 25px;
  padding-left: 25px;
}

.l-inner .house-image {
  display: block;
  margin: 0 auto 16px;
}

.l-inner .top--info {
  display: flex;
}

.l-inner .top--info .top--info--1 {
  width: 50%;
}

.l-inner .top--info .top--info--1 .top--info__text {
  background-color: #FFD664;
  width: 460px;
  margin-top: 16px;
  box-sizing: border-box;
  padding: 20px 20px 40px;
  line-height: 2rem;
}

.l-inner .top--info .top--info--1 .top--info__text .campany__name {
  line-height: 36px;
  font-size: 30px;
}

.l-inner .samples {
  display: flex;
  margin-left: 45px;
  margin-right: 45px;
  justify-content: space-between;
  margin-bottom: 145px;
}

.l-inner .samples .text-240 {
  border-radius: 15px;
  width: 240px;
  height: 265px;
  box-sizing: border-box;
  background-color: #FFD664;
  font-size: 16px;
  line-height: 28px;
  padding: 26px;
}

.l-inner h3 {
  font-size: 32px;
  text-align: center;
  font-weight: 200;
}

.l-inner .contact--items {
  display: flex;
  height: 300px;
  margin-top: 66px;
}

.l-inner .contact--items .contact--tel {
  width: 50%;
}

.l-inner .contact--items .contact--tel .contact--tel__inner {
  width: 180px;
  margin: 0 auto;
  text-align: center;
}

.l-inner .contact--items .contact--tel .contact--tel__inner .contact--tel__text {
  margin-top: 22px;
}

.l-inner .contact--items .contact--mail {
  width: 50%;
}

.l-inner .contact--items .contact--mail .contact--mail__inner {
  width: 180px;
  margin: 0 auto;
  text-align: center;
}

.l-inner .contact--items .contact--mail .contact--mail__inner .contact--mail__text {
  margin-top: 22px;
}

@media screen and (max-width: 1023px) {
  .l-inner {
    max-width: 33.75rem;
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.l-inner .top--image {
  position: relative;
  margin-bottom: 30px;
}

.l-inner .top--message {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
}

.l-inner .top-middle__menu {
  list-style: none;
  display: flex;
  width: 864px;
  margin: 0 auto;
  margin-bottom: 112px !important;
  justify-content: space-between;
}

.l-inner .top-middle__menu li {
  width: 100px;
  height: 50px;
}

.category-span .category-span__news {
  background-color: #F49600;
  color: #fff;
}

.category-span .category-span__event {
  background-color: #F20034;
  color: #fff;
}

.category-span .category-span__blog {
  background-color: #0DA030;
  color: #fff;
}

.more-btn {
  background-color: #000333;
  color: #fff;
  font-size: 21px;
  width: 180px;
  height: 50px;
  border-radius: 20px;
  display: block;
  line-height: 50px;
  text-align: center;
  text-decoration: none;
}

.top--about .top--about__text {
  width: 520px;
  height: auto;
  box-sizing: border-box;
  padding: 15px 30px 15px 30px;
  background-color: #FFD664;
  line-height: 35px;
}

.top--about .top--about__img {
  z-index: 2;
}

.top--about .top--about__h3 {
  font-size: 36px;
  font-weight: 100;
}

.about-table .about-table__title {
  font-size: 24px;
}

.about-table .about-table__body {
  font-size: 21px;
}

@media screen and (min-width: 1440px) {
  .u-hidden-pc {
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .u-hidden-sp {
    display: none;
  }
}
